<h1 id="android-fragments" tabindex="-1"><a class="header-anchor" href="#android-fragments" aria-hidden="true">#</a> Android Fragments</h1>
<p>With fragments we can re-use a part of the GUI in different activities or swap one part the GUI for another part.</p>
