<h3 id="static-counter-fragment" tabindex="-1"><a class="header-anchor" href="#static-counter-fragment" aria-hidden="true">#</a> Static counter fragment</h3>
<p>The <code>CounterFragment</code> contains a counter <code>Button</code> starting on 0. Its state is retained across runtime configuration changes. Three instances of it are statically inserted into the <code>MainActivity</code> using the <code>&lt;fragment&gt;</code> tag.</p>
