<h3 id="dynamic-countdown-fragment" tabindex="-1"><a class="header-anchor" href="#dynamic-countdown-fragment" aria-hidden="true">#</a> Dynamic countdown fragment</h3>
<p>The <code>CounterFragment</code> functions as a countdown. The start value is configurable and retained across runtime configuration changes using the arguments <code>Bundle</code>. Its state is retained across runtime configuration changes using the <code>savedInstanceState</code> <code>Bundle</code>. Three instances of it are dynamically inserted into the <code>MainActivity</code>.</p>
